import React, { Component } from 'react';

// react는 이벤트를 감싼 synteticBaseEvent사용
class Habit extends Component {
    // state = {
    //     count: 0,
    // };
    //전달받기 때문에 가지고있을 필요가 없다. 
    handleIncreament = () => {
        //state Object 안에 있는 count를 증가한뒤 state를 업데이트한다
        //this.state.count += 1 은 값은 변경되었는지 몰라도 화면에 표시가 안됨 변경되었는지 모르기 때문에 
        // this.setState({count: this.state.count + 1});

        //habits에서 부터로직
        this.props.onIncrement(this.props.habit);

    };

    handleDecreament = () => { 
        // const count = this.state.count -1 ;
        // this.setState({count: count <0 ? 0 : count});

        //habits.에서 부터 로직
        this.props.onDecrement(this.props.habit);

    };

    handelDelete = () => {
        this.props.onDelete(this.props.habit);
    }

    render() {
        //외부로 부터 받아온 데이터는 props 안에 있는 데이터에 저장되어진다. 
        console.log(this.props);
        const habitName = this.props.habit.name;
        const {name, count} = this.props.habit;
        return (
            <li className="habit">
                <span className="habit-name">{name}</span>
                <span className="habit-count">{count}</span>
                <button className="habit-button habit-increas" onClick={this.handleIncreament}>
                    <i className="fas fa-plus-square"></i>
                </button>
                <button className="habit-button habit-decrease" onClick={this.handleDecreament}>
                    <i className="fas fa-minus-square"></i>
                </button>
                <button className="habit-button habit-delete" onClick={this.handelDelete}>
                    <i className="fas fa-trash"></i>
                </button>
            </li>
           
        );
    } 
}

export default Habit;